:root {
  --color-primary: #333333;
  --color-secondary: #6cb71d;
  --color-secondary-dark: #497916;
  --color-white: #ffffff;
  --color-quinary: #34495e;
  --color-gray-light: #777777;
  --color-modal: #33333394;
  --color-footer: #4B4B4B;
  --color-gray: #bdbec0;
  --color-gray-border: #E4E4E4;
  --border-radius: 0.625rem;
  --color-close: #B8B8B8;
  --font-family-base: "Roboto", sans-serif;
  --font-size-small: 0.875rem; /* 14px */
  --font-size-base: 1rem; /* 16px */
  --font-size-medium: 1.125rem; /* 18px */
  --font-size-large: 1.25rem; /* 20px */
  --font-size-xlarge: 1.5rem; /* 24px */
  --font-size-xxlarge: 1.875rem; /* 30px */
  --font-size-3xlarge: 2rem; /* 32px */
  --font-size-title: 4rem; /* 64px */
}

@media (max-width: 768px) {
  :root {
    --font-size-title: 3rem; /* 40px */
    --font-size-xxlarge: 1.5rem; /* 24px */
    --font-size-xlarge: 1.25rem; /* 20px */
    --font-size-large: 1.125rem; /* 18px */
    --font-size-base: 1rem; /* 16px */
    --font-size-small: 0.875rem; /* 14px */
  }
}
@media (max-width: 425px) {
  :root {
    --font-size-title: 2.5rem; /* 40px */
  }
}
.container {
  width: 100%;
  max-width: clamp(300px, 90vw, 1200px); /* адаптивная ширина */
  margin: 0 auto;
}

@media (min-width: 1440px) {
  .swiper .swiper-button-next {
    right: 5.73vh;
  }
  .swiper .swiper-button-prev {
    left: 5.73vh;
  }
}
@media (max-width: 1440px) {
  .swiper .swiper-button-next {
    right: 1vh;
  }
  .swiper .swiper-button-prev {
    left: 1vh;
  }
}
@media (max-width: 1024px) {
  .benefits__body {
    flex-wrap: wrap;
  }
  .benefits__body .benefits__card {
    width: 45%;
  }
  .rations .rations__card {
    width: 45%;
  }
  .about .about__body {
    align-items: start;
  }
}
@media (max-width: 768px) {
  .swiper .swiper-wrapper .swiper-button-next {
    right: 1vh;
  }
  .swiper .swiper-wrapper .swiper-button-prev {
    left: 1vh;
  }
  .header__top-logo svg {
    width: 110px;
  }
  .header__top .header__top-contacts {
    justify-items: flex-end;
    text-align: right;
    justify-content: end;
  }
  .header__top-item > span {
    display: none;
  }
  .header__top-item svg {
    width: 1.875rem;
    height: 1.875rem;
    border-radius: 50%;
    padding: 5px;
    color: var(--color-white);
    background-color: var(--color-secondary);
  }
  .header__top-item:hover .header__item-sub {
    opacity: 1;
  }
  .rations .rations__card {
    width: 100%;
  }
  .about .about__body {
    flex-direction: column-reverse;
  }
  .about .about__img {
    width: 100%;
  }
  .delivery__body {
    flex-direction: column;
  }
  .delivery .delivery__img {
    border-radius: 0 0 14px 14px;
    width: 100%;
  }
}
@media (max-width: 660px) {
  .header__menu {
    position: fixed;
    flex-direction: column;
    align-items: flex-start;
    background-color: var(--color-white);
    left: -1000%;
    top: 0;
    width: 100%;
    height: 1000vh;
    z-index: 9999;
    transition: 0.2s;
  }
  .header__menu-link {
    font-weight: 700;
    font-size: var(--font-size-large);
  }
  .header__menu-link:first-child {
    margin-top: 50px;
  }
  .header__menu .menu__close {
    display: block;
    position: absolute;
    right: 30px;
    top: 15px;
  }
  .header__menu .menu__close svg {
    width: 30px;
    height: 30px;
  }
  .header__menu.active {
    left: 0;
  }
  .header .header__burger {
    display: block;
  }
  .swiper .swiper-button-next, .swiper .swiper-button-prev {
    display: none;
  }
  .benefits .benefits__body {
    justify-content: center;
  }
  .benefits__body .benefits__card {
    width: 80%;
  }
  .hero .hero__body .container {
    flex-direction: column-reverse;
    align-items: flex-start;
  }
  .delivery .delivery__item {
    padding: 40px;
  }
  .footer .footer__body-nav {
    width: 100%;
    justify-items: flex-start;
  }
  .footer .footer__body {
    row-gap: 20px;
  }
  .footer .footer__nav-list {
    justify-content: flex-start;
    gap: 20px;
  }
}
body {
  font-family: var(--font-family-base);
  background-color: var(--color-white);
  color: var(--color-primary);
  margin: 0;
}

body.lock {
  overflow: hidden;
}

.header {
  width: 100%;
  top: 0;
  left: 0;
  background-color: var(--color-white);
}

.header__top .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-top-logo {
  width: 100%;
  color: var(--color-secondary);
}

.header__top {
  padding: 1.375rem 0 1.125rem;
  border-bottom: 1px solid var(--color-gray);
}
.header__top-contacts {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1.25rem;
  width: 60%;
}
.header__top-item {
  position: relative;
  display: flex;
  align-items: center;
  gap: 0.5625rem;
  padding: 10px 0;
  white-space: nowrap;
  cursor: pointer;
}

.header__item-sub {
  opacity: 0;
  transition: 0.2s;
  bottom: -25px;
  left: -80%;
  background-color: var(--color-gray);
  position: absolute;
  padding: 5px 10px;
  border-radius: 4px;
  color: #000;
}

.header__item-sub.sub--telephone {
  left: -230%;
}

.header__item-sub.sub--last {
  bottom: -50px;
}

.header__bottom {
  position: sticky;
  top: 0;
  z-index: 1000;
  background: #fff;
  transition: 0.2s;
}

.header__bottom.fixed {
  position: fixed;
  width: 100%;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.header__body {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--color-white);
}

.menu__close {
  display: none;
}

.header__menu {
  display: flex;
  align-items: center;
  gap: 2.0625rem;
  padding: 1.0625rem 0;
}
.header__menu-link {
  padding: 0.625rem 0;
  font-size: 1.125rem;
}
.header__menu-link:hover {
  color: var(--color-gray-light);
}

.swiper .swiper-wrapper .swiper-slide {
  z-index: -1;
  position: relative;
}

.header__burger {
  display: none;
  width: 30px;
  height: 30px;
}
.header__burger-line {
  width: 100%;
  height: 3px;
  background-color: var(--color-primary);
  margin: 5px 0;
  transition: 0.2s;
  display: block;
}

.header__card {
  display: flex;
  align-items: center;
  gap: 1.25rem;
  padding: 0.625rem 0;
}
.header__card-icon {
  position: relative;
}
.header__card-count {
  position: absolute;
  top: -7px;
  right: -10px;
  width: 1.125rem;
  align-self: center;
  height: 1.125rem;
  color: var(--color-white);
  font-weight: 900;
  font-size: 0.75rem;
  background-color: var(--color-secondary);
  border-radius: 50%;
}

.hero__body {
  background: url("../img/hero-bg.png") no-repeat center center/cover;
}

.hero__body .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2.5rem 0;
}

.hero__content {
  z-index: 20;
  width: 50%;
  color: var(--color-white);
  text-transform: uppercase;
}
.hero__content-title {
  font-size: var(--font-size-title);
  font-weight: 800;
  line-height: 1;
}
.hero__content-title span {
  color: var(--color-secondary);
}
.hero__content-text {
  margin: 1.5rem 0;
  font-size: var(--font-size-large);
  color: var(--color-white);
  font-weight: 600;
}
.hero__content-text span.text--price {
  font-size: 32px;
  font-weight: 700;
}
.hero__content-button {
  display: inline-block;
  padding: 0.8125rem 3.25rem;
  background-color: var(--color-secondary);
  border-radius: var(--border-radius);
  font-weight: 700;
}
.hero__content-button:hover {
  background-color: var(--color-secondary-dark);
}

.swiper .swiper-button-next, .swiper .swiper-button-prev {
  color: #fff;
  width: 20px;
  height: 40px;
}

.swiper .swiper-pagination-bullet {
  background: transparent;
  border: 2px solid #fff;
  opacity: 1;
  width: 0.625rem;
  height: 0.625rem;
}

.swiper .swiper-pagination-bullet-active {
  background: #fff;
}

.benefits {
  margin: 5.75rem 0 6.5625rem;
}
.benefits__body {
  display: flex;
  justify-content: space-between;
  align-items: bottom;
  gap: 2.5rem;
}
.benefits__card {
  width: 33.3%;
  justify-items: center;
}
.benefits__card-text {
  text-align: center;
  color: #000;
  padding-top: 1.4875rem;
}

.section-title {
  text-transform: uppercase;
  font-weight: 700;
  font-size: var(--font-size-xxlarge);
}

.rations {
  margin: 6.5625rem 0 8.5625rem;
}
.rations__body {
  display: flex;
  justify-content: space-evenly;
  align-items: start;
  flex-wrap: wrap;
  margin-top: 2.5rem;
  gap: 1.25rem;
}
.rations__card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 32%;
  max-width: 17.5rem;
}
.rations__card-img {
  width: 17.5rem;
  height: 17.5rem;
  object-fit: cover;
}
.rations__card-head {
  height: 100%;
  margin-bottom: auto;
  height: 400px;
}
.rations__card-title {
  font-size: var(--font-size-medium);
  font-weight: 800;
  text-transform: uppercase;
  margin: 1.125rem 0 1.0625rem;
}
.rations__card-des {
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: var(--font-size-small);
}
.rations__card-ration {
  color: var(--colore-gray-light);
  font-size: var(--font-size-small);
  margin: 0.75rem 0 0.6875rem;
}
.rations__card-actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
  gap: 1.25rem;
  margin-top: auto;
  height: 5%;
}
.rations__card-price {
  font-size: var(--font-size-large);
  font-weight: 700;
  white-space: nowrap;
}
.rations__card-button {
  background-color: var(--color-secondary);
  color: var(--color-white);
  border-radius: var(--border-radius);
  padding: 0.5rem 2.3125rem;
}
.rations__card-button:hover {
  background-color: var(--color-secondary-dark);
}

.about {
  margin: 8.5625rem 0 5.75rem;
}
.about__body {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2.875rem;
}
.about__img {
  width: 50%;
}
.about__items-text {
  margin-top: 2.375rem;
}

.delivery {
  margin: 92px 0 104px;
}
.delivery__body {
  display: flex;
  border-radius: 14px;
  border: 1px solid var(--color-gray-border);
}
.delivery__img {
  width: 45%;
  object-fit: cover;
  border-radius: 0 14px 14px 0;
}
.delivery__item {
  width: 100%;
  padding: 5.625rem 4.375rem;
}
.delivery__item-text {
  margin: 2.625rem 0 1.9375rem;
}
.delivery__item-button {
  display: inline;
  background-color: var(--color-secondary);
  color: var(--color-white);
  padding: 0.5rem 1.625rem;
  font-size: 0.9375rem;
  font-weight: 700;
  border-radius: var(--border-radius);
}
.delivery__item-button:hover {
  background-color: var(--color-secondary-dark);
}

.footer {
  background-color: var(--color-footer);
  color: var(--color-white);
}
.footer__top {
  margin: 0 auto;
  width: 100%;
  max-width: 1506px;
  margin-left: auto;
  margin-right: auto;
  padding-left: clamp(8px, 4vw, 32px);
  padding-right: clamp(8px, 4vw, 32px);
  border-bottom: 1px solid #fff;
  padding: 1.875rem 0 2.375rem;
}
.footer__body {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
.footer__body-logo {
  width: 150px;
}
.footer__body-nav {
  width: 75%;
}
.footer__nav-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}
.footer__nav-link {
  display: flex;
  flex-direction: column;
  white-space: nowrap;
  line-height: 2.25rem;
}
.footer__nav-link:hover {
  color: var(--colore-gray-light);
}
.footer__items-body {
  padding: 1.375rem 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: start;
}
.footer__items-copy {
  display: flex;
  line-height: 1.5rem;
  justify-content: space-between;
  align-items: start;
  width: 100%;
}

.sub {
  display: none;
  opacity: 0;
  position: fixed;
  width: 100%;
  height: 100vh;
  z-index: 200;
  top: 0;
  left: 0;
  background: var(--color-modal);
  transition: 0.2s;
}
.sub__body {
  background-color: var(--color-white);
  padding: 3.75rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.sub__title {
  text-align: center;
  font-size: var(--font-size-xlarge);
  text-transform: uppercase;
  font-weight: 700;
}
.sub__inputs {
  margin: 2.5rem 0 0;
}
.sub__inputs > label {
  font-size: var(--font-size-small);
}
.sub__inputs label span {
  color: var(--color-secondary);
}
.sub__inputs input {
  width: 400px;
  height: 40px;
  border: 1px solid var(--color-gray-border);
  padding: 8px 15px;
  margin: 7px 0 20px;
}
.sub__body-button {
  padding: 13px 40px;
  background-color: var(--color-secondary);
  color: var(--color-white);
  display: block;
  border-radius: var(--border-radius);
  font-weight: 700;
  margin: 30px auto;
}
.sub__body-button:hover {
  background-color: var(--color-secondary-dark);
}
.sub__sub {
  text-align: center;
}
.sub__sub a {
  color: var(--color-secondary);
}

.sub__close {
  position: absolute;
  right: 1.25rem;
  top: 1.25rem;
  transition: 0.2s;
  color: var(--color-modal);
}

.sub__close:hover {
  color: #000;
}

.sub.active {
  opacity: 1;
  display: block;
}

.succes {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--color-modal);
  z-index: 1000;
}
.succes.active {
  display: block;
}

.modal-content {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #fff;
  padding: 60px 105px;
  width: 520px;
  text-align: center;
}
.modal-content p {
  margin: 40px 0;
}
.modal-content .btn {
  margin-top: 1rem;
}